import React from 'react';
import {
    Footer as FooterList,
    FooterItem
} from '../../components/SidebarComponent'

const Footer = () => {
    const data = [
        'Gii thiu',
        'Bang tin',
        'Lin h',
        'S nghip',
        'BytaDance',
        'TikTok for Good',
        'Advertise',
        'Developers',
        'Transparency',
        'Tr gip',
        'An ton',
        'iu khon',
        'Quyn ring t',
        'Creator Portal',
        'Hng dn Cng ng',
        'More',
        ' 2021 TikTok'
    ]
    return (
        <FooterList>
            {data.map((tag, index) => (
                <FooterItem
                    key={index}
                    tag={tag}
                />
            ))}
        </FooterList>
    );
};

export default Footer;